/*
 *
 * RoleDetail
 *
 */

import React from 'react';
import styled from 'styled-components';
import { keyBy, some } from 'lodash';

import SimplePerms from 'components/PermissionsCardForSimpleAssets';
import ListPerms from 'components/PermissionsCardForLists';

import {
  UserIcon,
  DevicesIcon,
  DeployIcon,
  DatabaseIcon,
  PortalIcon,
  CodeIcon,
  MessagesIcon,
  TriggerIcon,
  TimerIcon,
  EdgesIcon,
  RolesIcon,
} from 'components/EntityIcons';
import messages from 'containers/Console/messages';
import detailMessages from './messages';
import CardTable from 'components/CardTable';
import {
  RoleModel,
  PermissionsModel,
  Entities,
  CollectionModel,
  Permissions as PermissionsEnum,
  ServiceModel,
  AllPermissionsCodeForPortals,
  RemovablePermissions,
  AllPermissionsCodeForTopics,
  BasicListPermissionModel,
} from 'cb-utils/console-entity-models';
import { CollectionsModel, EntityModel } from '../Console/state';
import { PortalModel } from 'cb-utils/console-entity-models/portals';
import { FormattedMessage } from 'react-intl';
import { Jumbotron } from 'reactstrap';
import TypeaheadWithSubmit from 'components/TypeaheadWithSubmit';
import { getAllMessageTopics } from 'cb-utils/services/messages';

const JumbotronWrapper = styled(Jumbotron)`
  padding: 2rem;
`;

const Wrapper = styled.div`
  min-width: 950px;
`;

interface PermissionsMatrixProps {
  role: RoleModel;
  collections: CollectionsModel;
  portals: Entities<PortalModel>;
  services: Entities<ServiceModel>;
  onChange: (
    key: keyof PermissionsModel,
    level: number,
    assetId?: string,
    assetName?: string,
  ) => void;
  onChangeAll: (
    key: keyof PermissionsModel,
    list: EntityModel,
    level: number,
  ) => void;
  removePerm: (type: RemovablePermissions, id: string) => void;
}

interface IState {
  allTopics: string[];
}

export class PermissionsMatrix extends React.Component<
  PermissionsMatrixProps,
  {}
> {
  state: IState = {
    allTopics: [],
  };

  componentDidMount() {
    getAllMessageTopics().then((allTopics) => this.setState({ allTopics }));
  }

  render() {
    const {
      role: { Permissions },
      onChange,
      onChangeAll,
      collections,
      portals,
      services,
      removePerm,
    } = this.props;
    const topics = {
      loading: {},
      hasFetched: false,
      entities: keyBy(Permissions.Topics, 'Name'),
    };
    return (
      <Wrapper>
        <JumbotronWrapper>
          <h4>
            <FormattedMessage {...detailMessages.perItem} />
          </h4>
          <CardTable>
            <ListPerms
              icon={DatabaseIcon}
              title={messages.assetClassCollections}
              perms={Permissions.Collections}
              list={collections}
              onChange={(level: number, collection: CollectionModel) =>
                onChange(
                  'Collections',
                  level,
                  collection.collectionID,
                  collection.name,
                )
              }
              onChangeAll={(level: number) =>
                onChangeAll('Collections', collections, level)
              }
              assetClass="collections"
            />
            <ListPerms
              icon={PortalIcon}
              title={messages.assetClassPortals}
              perms={Permissions.Portals}
              list={portals}
              allPermissionsCode={AllPermissionsCodeForPortals}
              onChange={(level: number, portal: PortalModel) =>
                onChange('Portals', level, portal.name, portal.name)
              }
              onChangeAll={(level: number) =>
                onChangeAll('Portals', portals, level)
              }
              assetClass="portals"
            />
            <ListPerms
              icon={CodeIcon}
              title={messages.assetClassServices}
              perms={Permissions.CodeServices}
              list={services}
              showAll={false}
              readPermissionText={messages.runPermission}
              allPermissionsCode={PermissionsEnum.Read}
              onChange={(level: number, service: ServiceModel) =>
                onChange('CodeServices', level, service.name, service.name)
              }
              onChangeAll={(level: number) =>
                onChangeAll('CodeServices', services, level)
              }
              assetClass="services"
            />
            <div data-cy="topics-card">
              <ListPerms
                icon={MessagesIcon}
                title={messages.messageTopics}
                nameLabelHelp={detailMessages.topicHelp}
                perms={Permissions.Topics}
                list={topics}
                readPermissionText={messages.subscribePermission}
                createPermissionText={messages.publishPermission}
                allPermissionsCode={AllPermissionsCodeForTopics}
                onChange={(level: number, perm: BasicListPermissionModel) =>
                  onChange('Topics', level, perm.Name, perm.Name)
                }
                onChangeAll={(level: number) =>
                  onChangeAll('Topics', topics, level)
                }
                removePerm={(id: string) =>
                  removePerm(RemovablePermissions.TOPIC, id)
                }
                assetClass="topics"
                alternateText={this.getAlternateTextForTopics()}
                forceAllowOpen
                cardFooterContent={
                  <div className="py-4 pl-3">
                    <TypeaheadWithSubmit
                      options={this.state.allTopics
                        .filter((t) => !some(Permissions.Topics, { Name: t }))
                        .map((t) => ({
                          name: t,
                          value: t,
                        }))}
                      onSubmit={(val) =>
                        onChange(
                          'Topics',
                          AllPermissionsCodeForTopics,
                          val,
                          val,
                        )
                      }
                      emptyLabel={'Add new topic'}
                      addMessage={detailMessages.addTopic}
                    />
                  </div>
                }
              />
            </div>
          </CardTable>
        </JumbotronWrapper>
        <JumbotronWrapper>
          <h4>
            <FormattedMessage {...detailMessages.overall} />
          </h4>
          <div data-cy="overall-perms">
            <CardTable>
              <SimplePerms
                icon={UserIcon}
                title={messages.assetClassUsers}
                perms={Permissions.UsersList}
                onChange={(level) => onChange('UsersList', level)}
                readHelpText={detailMessages.userReadHelp}
                updateHelpText={detailMessages.userUpdateHelp}
                createHelpText={detailMessages.userCreateHelp}
                deleteHelpText={detailMessages.userDeleteHelp}
              />
              <SimplePerms
                icon={DevicesIcon}
                title={messages.assetClassDevices}
                perms={Permissions.DevicesList}
                onChange={(level) => onChange('DevicesList', level)}
                readHelpText={detailMessages.deviceReadHelp}
                updateHelpText={detailMessages.deviceUpdateHelp}
                createHelpText={detailMessages.deviceCreateHelp}
                deleteHelpText={detailMessages.deviceDeleteHelp}
              />
              <SimplePerms
                icon={MessagesIcon}
                title={messages.assetClassMessageHistory}
                perms={Permissions.MsgHistory}
                allPermissionsCode={
                  PermissionsEnum.Read + PermissionsEnum.Delete
                }
                onChange={(level) => onChange('MsgHistory', level)}
                readHelpText={detailMessages.msgHistoryReadHelp}
                deleteHelpText={detailMessages.msgHistoryDeleteHelp}
              />
              <SimplePerms
                icon={DatabaseIcon}
                title={messages.assetClassCollections}
                perms={Permissions.AllCollections}
                onChange={(level) => onChange('AllCollections', level)}
                readHelpText={detailMessages.collectionReadHelp}
                updateHelpText={detailMessages.collectionUpdateHelp}
                createHelpText={detailMessages.collectionCreateHelp}
                deleteHelpText={detailMessages.collectionDeleteHelp}
              />
              <SimplePerms
                icon={CodeIcon}
                title={messages.assetClassServices}
                perms={Permissions.AllServices}
                onChange={(level) => onChange('AllServices', level)}
                readHelpText={detailMessages.serviceReadHelp}
                updateHelpText={detailMessages.serviceUpdateHelp}
                createHelpText={detailMessages.serviceCreateHelp}
                deleteHelpText={detailMessages.serviceDeleteHelp}
              />
              <SimplePerms
                icon={EdgesIcon}
                title={messages.assetClassEdges}
                perms={Permissions.EdgesList}
                onChange={(level) => onChange('EdgesList', level)}
                readHelpText={detailMessages.edgeReadHelp}
                updateHelpText={detailMessages.edgeUpdateHelp}
                createHelpText={detailMessages.edgeCreateHelp}
                deleteHelpText={detailMessages.edgeDeleteHelp}
              />
              <SimplePerms
                icon={TriggerIcon}
                title={messages.assetClassTriggers}
                perms={Permissions.Triggers}
                onChange={(level) => onChange('Triggers', level)}
                readHelpText={detailMessages.triggerReadHelp}
                updateHelpText={detailMessages.triggerUpdateHelp}
                createHelpText={detailMessages.triggerCreateHelp}
                deleteHelpText={detailMessages.triggerDeleteHelp}
              />
              <SimplePerms
                icon={TimerIcon}
                title={messages.assetClassTimers}
                perms={Permissions.Timers}
                onChange={(level) => onChange('Timers', level)}
                readHelpText={detailMessages.timerReadHelp}
                updateHelpText={detailMessages.timerUpdateHelp}
                createHelpText={detailMessages.timerCreateHelp}
                deleteHelpText={detailMessages.timerDeleteHelp}
              />
              <SimplePerms
                icon={DeployIcon}
                title={messages.assetClassDeployments}
                perms={Permissions.Deployments}
                onChange={(level) => onChange('Deployments', level)}
                readHelpText={detailMessages.deploymentReadHelp}
                updateHelpText={detailMessages.deploymentUpdateHelp}
                createHelpText={detailMessages.deploymentCreateHelp}
                deleteHelpText={detailMessages.deploymentDeleteHelp}
              />
              <SimplePerms
                icon={RolesIcon}
                title={messages.assetClassRoles}
                perms={Permissions.Roles}
                onChange={(level) => onChange('Roles', level)}
                readHelpText={detailMessages.roleReadHelp}
                updateHelpText={detailMessages.roleUpdateHelp}
                createHelpText={detailMessages.roleCreateHelp}
                deleteHelpText={detailMessages.roleDeleteHelp}
              />
            </CardTable>
          </div>
        </JumbotronWrapper>
      </Wrapper>
    );
  }

  handleChangeAll = (
    key: keyof PermissionsModel,
    list: EntityModel,
    level: number,
    assetIdKey: string,
    assetNameKey: string,
  ) => {
    const entities = list.entities;
    for (const id in entities) {
      if (entities.hasOwnProperty(id)) {
        this.props.onChange(
          key,
          level,
          entities[id][assetIdKey],
          entities[id][assetNameKey],
        );
      }
    }
  };

  getAlternateTextForTopics = () => {
    const topicPerms = this.props.role.Permissions.Topics;
    if (topicPerms.length === 0) {
      return <FormattedMessage {...detailMessages.openToConfig} />;
    }
  };
}

export default PermissionsMatrix;



