/*
 *
 * RoleDetail
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import { Button } from 'reactstrap';
import injectReducer from 'utils/injectReducer';
import { selectHasLoaded } from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { createInitRoleDetail, createDeleteRoleRequest } from './actions';
import {
  RoleModel,
  PermissionsModel,
  RemovablePermissions,
} from 'cb-utils/console-entity-models';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import PageFooter from 'components/ConsolePageFooter';
import LoadingIndicator from 'components/LoadingIndicator';
import Matrix from './PermissionsMatrix';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import consoleMessages from 'containers/Console/messages';
import { EntityModel } from 'containers/Console/state';
import {
  portalSelectors,
  collectionSelectors,
  serviceSelectors,
} from 'containers/Console/ducks';
import {
  createSaveRoleRequest,
  createRemovePermRequest,
} from 'containers/Console/actions/roles';
import { TrashIcon } from 'components/EntityIcons';
import { createShowConfirmationModal } from 'containers/Console/actions/ui';
import DescEditor from 'components/EntityDescriptionEditor';
import { CBDispatch } from 'cb-utils/typeUtils';
import {
  updateModifiableRole,
  updatePermissions,
  createResetModifiableItems,
} from 'containers/Console/actions/modifiableEnitites';
import {
  selectModifiableItem,
  selectModifiableItemTouched,
} from '../Console/selectors/modifiableEnitites';
import CbPrompt, { confirmCancelHash } from 'components/CbPrompt';
import { overallUrls } from 'cb-utils/getUrls';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

type RoleDetailProps = SystemRouteProps<{
  roleName: string;
}>;

const getRoleName = (props: RoleDetailProps): string =>
  props.match.params.roleName;

export class RoleDetail extends React.PureComponent<
  RoleDetailProps &
    DispatchToProps &
    StateToProps &
    ReactIntl.InjectedIntlProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function

  componentWillMount() {
    this.props.init();
  }

  componentWillUnmount() {
    this.props.createResetModifiableRoles();
  }

  render() {
    return (
      <React.Fragment>
        <CbPrompt
          when={this.props.hasRoleChanged}
          message={this.props.intl.formatMessage(messages.warnLeaveMessage)}
        />
        <ConsolePage subheader={this._renderSubHeader()}>
          {this.props.role ? (
            <div>
              <Matrix
                role={this.props.role}
                collections={this.props.collections}
                portals={this.props.portals}
                services={this.props.services}
                onChange={(
                  key: keyof PermissionsModel,
                  level: number,
                  assetId?: string,
                  assetName?: string,
                ) =>
                  this.props.permissionChange(this.props.role, key, level, [
                    {
                      name: assetName,
                      collectionID: assetId,
                    },
                  ])
                }
                onChangeAll={(
                  key: keyof PermissionsModel,
                  list: EntityModel,
                  level: number,
                ) =>
                  this.props.permissionChange(
                    this.props.role,
                    key,
                    level,
                    // @ts-ignore
                    Object.values(list.entities),
                  )
                }
                removePerm={(type: RemovablePermissions, id: string) =>
                  this.props.removePerm(
                    this.props.intl.formatMessage(
                      messages.removePermConfirmation,
                      {
                        name: id,
                      },
                    ),
                    type,
                    id,
                  )
                }
              />
            </div>
          ) : (
            <LoadingIndicator forPage />
          )}
        </ConsolePage>
        <PageFooter
          leftButtons={this.renderDeleteButton}
          actionButtons={this.renderActionButtons}
        />
      </React.Fragment>
    );
  }

  _renderSubHeader = () => {
    const roleName = this.props.role && this.props.role.Name;
    if (roleName) {
      return (
        <React.Fragment>
          <PageHeader
            header={<div>{roleName}</div>}
            subHeader={
              <DescEditor
                value={this.props.role.Description}
                onChange={this.props.onDescriptionChange}
                defaultValue="Description"
              />
            }
          />
        </React.Fragment>
      );
    }
    return null;
  };

  renderActionButtons = () => {
    return (
      <React.Fragment>
        <Button
          color="default"
          onClick={() =>
            this.props.history.push(overallUrls.roles({}, confirmCancelHash))
          }
        >
          <FormattedMessage {...consoleMessages.discardChanges} />
        </Button>
        <Button
          color="primary"
          data-cy="save-role"
          onClick={() => this.props.saveRoleRequest(this.props.role)}
        >
          <FormattedMessage {...consoleMessages.saveAndExit} />
        </Button>
      </React.Fragment>
    );
  };

  renderDeleteButton = () => {
    const { role, deleteRoleRequest } = this.props;
    if (role) {
      return (
        <Button
          color="link"
          onClick={() => deleteRoleRequest(role)}
          data-cy="delete-role"
        >
          <TrashIcon />
          &nbsp;
          <FormattedMessage {...messages.deleteRole} />
        </Button>
      );
    }
    return null;
  };
}

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapStateToProps = (state: AppState, ownProps: RoleDetailProps) => {
  const roleName = getRoleName(ownProps);
  return createStructuredSelector({
    hasRoleChanged: selectModifiableItemTouched({
      assetClass: 'roles',
      id: roleName,
    }),
    role: selectModifiableItem<RoleModel>('roles', roleName),
    hasLoaded: selectHasLoaded(),
    collections: collectionSelectors.wholeSlice,
    portals: portalSelectors.wholeSlice,
    services: serviceSelectors.wholeSlice,
  });
};

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

function mapDispatchToProps(dispatch: CBDispatch, ownProps: RoleDetailProps) {
  const sysKey = ownProps.match.params.systemKey;
  const roleName = getRoleName(ownProps);
  return {
    init: () => {
      dispatch(createInitRoleDetail(sysKey, roleName));
    },
    createResetModifiableRoles: () =>
      dispatch(createResetModifiableItems({ assetClass: 'roles' })),
    permissionChange: ((...args: any[]) =>
      // @ts-ignore
      dispatch(updatePermissions(...args))) as typeof updatePermissions,
    saveRoleRequest: (role: RoleModel) =>
      dispatch(createSaveRoleRequest(sysKey, role)),
    deleteRoleRequest: (role: RoleModel) =>
      dispatch(
        createShowConfirmationModal(
          `Are you sure you want to delete '${role.Name}'?`,
          createDeleteRoleRequest(sysKey, role),
        ),
      ),
    onDescriptionChange: (Description: string) =>
      dispatch(updateModifiableRole({ name: roleName, Description })),
    removePerm: (
      confirmationMessage: string,
      permType: RemovablePermissions,
      permId: string,
    ) =>
      dispatch(
        createShowConfirmationModal(
          confirmationMessage,
          createRemovePermRequest(sysKey, roleName, permType, permId),
        ),
      ),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect, injectIntl)(
  RoleDetail,
);



