/*
 *
 * RoleDetail actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import { RoleModel } from 'cb-utils/console-entity-models';

export interface InitRoleDetail extends BaseAction {
  type: ActionConstants.INIT_ROLE_DETAIL;
  payload: {
    systemKey?: string;
    name?: string;
  };
}

export const createInitRoleDetail = (
  systemKey?: string,
  name?: string,
): InitRoleDetail => ({
  type: ActionConstants.INIT_ROLE_DETAIL,
  payload: {
    systemKey,
    name,
  },
});

export interface DeleteRoleRequest extends BaseAction {
  type: ActionConstants.DELETE_ROLE_REQUEST;
  payload: {
    systemKey: string;
    role: RoleModel;
  };
}

export const createDeleteRoleRequest = (
  systemKey: string,
  role: RoleModel,
): DeleteRoleRequest => ({
  type: ActionConstants.DELETE_ROLE_REQUEST,
  payload: {
    systemKey,
    role,
  },
});

export interface DeleteRoleSuccess extends BaseAction {
  type: ActionConstants.DELETE_ROLE_SUCCESS;
  payload: {
    systemKey: string;
    role: RoleModel;
  };
}

export const createDeleteRoleSuccess = (
  systemKey: string,
  role: RoleModel,
): DeleteRoleSuccess => ({
  type: ActionConstants.DELETE_ROLE_SUCCESS,
  payload: {
    systemKey,
    role,
  },
});

export interface DeleteRoleError extends BaseAction {
  type: ActionConstants.DELETE_ROLE_ERROR;
  payload: {
    error: any;
  };
}

export const createDeleteRoleError = (error: any): DeleteRoleError => ({
  type: ActionConstants.DELETE_ROLE_ERROR,
  payload: {
    error,
  },
});

export type ActionTypes = InitRoleDetail;



