/**
 *
 * PermissionsCardForLists
 *
 */

import React from 'react';
import styled from 'styled-components';
import { find } from 'lodash';
import { Button } from 'reactstrap';

import { FormattedMessage } from 'react-intl';
import consoleMessages from 'containers/Console/messages';
import messages from './messages';

import PermCard from 'components/PermissionsCard';
import { CloseIcon } from 'components/Icons';
import {
  PermissionPrimitive,
  BasicListPermissionModel,
  Permissions,
  canPerformOperation,
  Entities,
} from 'cb-utils/console-entity-models';
import CheckMarkIcon from 'react-icons/lib/fa/check';
import { EntityModel } from 'containers/Console/state';

import PermRow from 'components/CrudpermissionsRow';
import TitleSection from 'components/PermissionsCardTitleSection';

import FixedTable from 'components/PermissionsCardTable';
import PermField from 'components/PermissionFieldCheckbox';

import HelpMe from 'components/HelpPrimitive';

const NameHeader = styled.span`
  font-weight: bold;
`;

interface PermissionsCardForListsProps<T> {
  icon: React.ComponentClass;
  title: FormattedMessage.MessageDescriptor;
  perms: BasicListPermissionModel[];
  list: Entities<T>;
  onChange: (level: number, item: T) => void;
  onChangeAll: (level: number) => void;
  assetClass: string;
  alternateText?: JSX.Element;
  readPermissionText?: FormattedMessage.MessageDescriptor;
  createPermissionText?: FormattedMessage.MessageDescriptor;
  showAll?: boolean;
  allPermissionsCode?: number;
  removePerm?: (id: string) => void;
  forceAllowOpen?: boolean;
  cardFooterContent?: JSX.Element;
  nameLabelHelp?: FormattedMessage.MessageDescriptor;
}

const getRatioForOperation = (num: number, total: number): JSX.Element => {
  if (num === 0) {
    return <b>0</b>;
  } else if (num === total) {
    return <CheckMarkIcon />;
  } else {
    return <b>{`${num}/${total}`}</b>;
  }
};

const getNumberOfItemsThatCanPerformOperation = (
  list: EntityModel,
  perms: BasicListPermissionModel[],
  op: Permissions,
): number => {
  let rtn = 0;
  const entities = list.entities;
  let found: BasicListPermissionModel;
  for (const key in entities) {
    if (entities.hasOwnProperty(key)) {
      found = find(perms, { Name: key });
      if (found && canPerformOperation(found.Level, op)) {
        rtn += 1;
      }
    }
  }
  return rtn;
};

const getTotalNumberOfEntities = (list: EntityModel): number =>
  Object.keys(list.entities).length;

class PermissionsCardForLists extends React.PureComponent<
  PermissionsCardForListsProps<{}>,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<PermissionsCardForListsProps<{}>> = {
    showAll: true,
    allPermissionsCode: Permissions.All,
    readPermissionText: consoleMessages.readPermission,
    createPermissionText: consoleMessages.createPermission,
    forceAllowOpen: false,
  };

  render() {
    const { forceAllowOpen, cardFooterContent } = this.props;
    const hasEntities = this.getTotal() > 0;
    return (
      <PermCard
        header={this.renderHeader()}
        table={this.renderTable()}
        allowHoverEffect={hasEntities || forceAllowOpen}
        showToggleIcon={hasEntities || forceAllowOpen}
        cardFooterContent={cardFooterContent}
      />
    );
  }

  fetchNumeratorFactory = (permCode: Permissions) => (): number =>
    getNumberOfItemsThatCanPerformOperation(
      this.props.list,
      this.props.perms,
      permCode,
    );

  getReadableEntities = this.fetchNumeratorFactory(Permissions.Read);
  getUpdateableEntities = this.fetchNumeratorFactory(Permissions.Update);
  getCreatableEntities = this.fetchNumeratorFactory(Permissions.Create);
  getDeletableEntities = this.fetchNumeratorFactory(Permissions.Delete);

  getTotal = () => getTotalNumberOfEntities(this.props.list);

  renderHeader = () => {
    const {
      readPermissionText,
      createPermissionText,
      allPermissionsCode,
    } = this.props;
    const readableEntities = this.getReadableEntities();
    const updateableEntities = this.getUpdateableEntities();
    const creatableEntities = this.getCreatableEntities();
    const deletableEntities = this.getDeletableEntities();
    return (
      <PermRow
        label={<TitleSection icon={this.props.icon} title={this.props.title} />}
        alternateText={this.getAlternateText(
          readableEntities +
            updateableEntities +
            creatableEntities +
            deletableEntities ===
            Permissions.None,
        )}
        showAll={false}
        readPermissionText={readPermissionText}
        readField={this.renderField(readableEntities, readPermissionText)}
        createField={this.renderField(creatableEntities, createPermissionText)}
        updateField={this.renderField(
          updateableEntities,
          consoleMessages.updatePermission,
        )}
        deleteField={this.renderField(
          deletableEntities,
          consoleMessages.deletePermission,
        )}
        allPermissionsCode={allPermissionsCode}
      />
    );
  };

  getAlternateText = (noPerms: boolean) => {
    if (this.props.alternateText) {
      return this.props.alternateText;
    } else if (this.getTotal() === 0) {
      return (
        <FormattedMessage
          {...messages.noEntitiesInSystem}
          values={{ assetClass: this.props.assetClass }}
        />
      );
    } else if (noPerms) {
      return <FormattedMessage {...messages.noPermissionsAdded} />;
    }
    return null;
  };

  renderField = (num: number, message: FormattedMessage.MessageDescriptor) => {
    const totalItems = this.getTotal();
    return (
      <div>
        {getRatioForOperation(num, totalItems)}
        <FormattedMessage {...message} />
      </div>
    );
  };

  renderTable = () => {
    const {
      showAll,
      allPermissionsCode,
      readPermissionText,
      createPermissionText,
      nameLabelHelp,
    } = this.props;
    const totalEntities = getTotalNumberOfEntities(this.props.list);
    return (
      <FixedTable size="sm" striped>
        <thead>
          <PermRow
            label={
              <NameHeader>
                <FormattedMessage {...consoleMessages.name} />
                {nameLabelHelp && (
                  <HelpMe body={<FormattedMessage {...nameLabelHelp} />} />
                )}
              </NameHeader>
            }
            showAll={showAll}
            cellTag="th"
            readField={this.renderTableHeaderField(
              Permissions.Read,
              readPermissionText,
              totalEntities,
            )}
            updateField={this.renderTableHeaderField(
              Permissions.Update,
              consoleMessages.updatePermission,
              totalEntities,
            )}
            createField={this.renderTableHeaderField(
              Permissions.Create,
              createPermissionText,
              totalEntities,
            )}
            deleteField={this.renderTableHeaderField(
              Permissions.Delete,
              consoleMessages.deletePermission,
              totalEntities,
            )}
            allField={this.renderTableHeaderField(
              allPermissionsCode,
              consoleMessages.allPermissions,
              totalEntities,
            )}
            allPermissionsCode={allPermissionsCode}
          />
        </thead>
        <tbody>{this.renderRows()}</tbody>
      </FixedTable>
    );
  };

  renderTableHeaderField = (
    permCode: Permissions,
    message: FormattedMessage.MessageDescriptor,
    totalEntities: number,
  ) => {
    const canDoTheThing =
      getNumberOfItemsThatCanPerformOperation(
        this.props.list,
        this.props.perms,
        permCode,
      ) === totalEntities;
    return (
      <PermField
        checked={canDoTheThing}
        onChange={() => {
          let code = permCode;
          if (canDoTheThing) {
            code = -permCode;
          }
          this.props.onChangeAll(code);
        }}
        message={message}
      />
    );
  };

  getPermissionsForItem = (name: string): PermissionPrimitive => {
    for (let i = 0, len = this.props.perms.length; i < len; i += 1) {
      if (this.props.perms[i].Name === name) {
        return {
          Level: this.props.perms[i].Level,
        };
      }
    }
    return {
      Level: 0,
    };
  };

  renderRows = () => {
    const {
      list,
      showAll,
      onChange,
      allPermissionsCode,
      readPermissionText,
      createPermissionText,
      removePerm,
    } = this.props;
    const rtn = [];
    for (const name in list.entities) {
      if (list.entities.hasOwnProperty(name)) {
        rtn.push(
          <PermRow
            key={name}
            label={
              removePerm ? (
                <span>
                  <Button
                    data-cy="remove-perm"
                    size="sm"
                    onClick={() => removePerm(name)}
                  >
                    <CloseIcon />
                  </Button>
                  &nbsp;
                  {name}
                </span>
              ) : (
                name
              )
            }
            perms={this.getPermissionsForItem(name)}
            allPermissionsCode={allPermissionsCode}
            readPermissionText={readPermissionText}
            createPermissionText={createPermissionText}
            showAll={showAll}
            onChange={(level) => onChange(level, list.entities[name])}
          />,
        );
      }
    }
    return rtn;
  };
}

export default PermissionsCardForLists;



