/**
 *
 * SimplePermissionsCard
 *
 */

import React from 'react';

import { FormattedMessage } from 'react-intl';

import TitleSection from 'components/PermissionsCardTitleSection';
import PermRow from 'components/CrudpermissionsRow';
import {
  PermissionPrimitive,
  Permissions,
} from 'cb-utils/console-entity-models';
import PermCard from 'components/PermissionsCard';

interface SimplePermissionsCardProps {
  icon: React.ComponentClass;
  title: FormattedMessage.MessageDescriptor;
  perms: PermissionPrimitive;
  onChange: (level: number) => void;
  readHelpText?: FormattedMessage.MessageDescriptor;
  createHelpText?: FormattedMessage.MessageDescriptor;
  updateHelpText?: FormattedMessage.MessageDescriptor;
  deleteHelpText?: FormattedMessage.MessageDescriptor;
  allPermissionsCode?: number;
}

class SimplePermissionsCard extends React.PureComponent<
  SimplePermissionsCardProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<SimplePermissionsCardProps> = {
    allPermissionsCode: Permissions.All,
  };

  render() {
    // there's the possibility that a perms field doesn't exist in the API response
    if (this.props.perms) {
      return <PermCard header={this.renderHeader()} allowHoverEffect={false} />;
    }
    return null;
  }

  renderHeader = () => {
    const {
      icon,
      title,
      perms,
      onChange,
      readHelpText,
      updateHelpText,
      createHelpText,
      deleteHelpText,
      allPermissionsCode,
    } = this.props;
    return (
      <PermRow
        label={<TitleSection icon={icon} title={title} />}
        perms={perms}
        onChange={onChange}
        readHelpText={readHelpText}
        updateHelpText={updateHelpText}
        deleteHelpText={deleteHelpText}
        createHelpText={createHelpText}
        allPermissionsCode={allPermissionsCode}
      />
    );
  };
}

export default SimplePermissionsCard;



