import { defineMessages, Messages } from 'react-intl';

const mess: Messages = defineMessages({
  noPermissionsAdded: {
    id: 'app.containers.Console.noPermissionsAdded',
    defaultMessage: 'No permissions added',
  },
  noEntitiesInSystem: {
    id: 'app.containers.Console.noEntitiesInSystem',
    defaultMessage: 'No {assetClass} in system',
  },
});

export default mess;



