/**
 *
 * HoverCard
 *
 */

import React from 'react';
// import styled from 'styled-components';

import Card from 'material-ui-next/Card';

interface HoverCardProps {
  showPointerCursor?: boolean;
  className?: string;
  raised?: boolean; // allows us to override the internal state if we want to
}

interface HoverCardState {
  raised: boolean;
}

class HoverCard extends React.Component<HoverCardProps, HoverCardState> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<HoverCardProps> = {
    showPointerCursor: false,
    raised: false,
  };

  state: HoverCardState = {
    raised: false,
  };

  onMouseOver = () => this.setState({ raised: true });

  onMouseOut = () => this.setState({ raised: false });

  render() {
    const { showPointerCursor, className } = this.props;
    return (
      <Card
        raised={this.props.raised || this.state.raised}
        onMouseOver={this.onMouseOver}
        onMouseOut={this.onMouseOut}
        className={`${className} ${showPointerCursor ? 'c-pointer' : ''}`}
      >
        {this.props.children}
      </Card>
    );
  }
}

export default HoverCard;



