import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the roleDetail state domain
 */
const selectRoleDetailDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const selectHasLoaded = () =>
  createSelector(makeSelectRoleDetail(), (substate) => substate.hasLoaded);

/**
 * Default selector used by RoleDetail
 */

const makeSelectRoleDetail = () =>
  createSelector(selectRoleDetailDomain(), (substate) => substate);

export default makeSelectRoleDetail;
export { selectRoleDetailDomain, selectHasLoaded };



