/**
 *
 * PermissionsCard
 *
 */

import React from 'react';
import styled from 'styled-components';

import Card, { CardContent, CardActions } from 'material-ui-next/Card';
import HoverCard from 'components/HoverCard';
import { Collapse, Form } from 'reactstrap';
import ChevronDown from 'react-icons/lib/fa/chevron-circle-down';
import ChevronUp from 'react-icons/lib/fa/chevron-circle-up';

import FixedTable from 'components/PermissionsCardTable';

const ToggleContainer = styled.div`
  font-size: 1.25rem;
  position: absolute;
  right: 0;
`;

const HeaderRow = styled.div`
  position: relative;
`;

interface PermissionsCardProps {
  header: JSX.Element;
  table?: JSX.Element;
  showToggleIcon?: boolean;
  allowHoverEffect?: boolean;
  cardFooterContent?: JSX.Element;
}

interface PermissionsCardState {
  isExpanded: boolean;
}

class PermissionsCard extends React.Component<
  PermissionsCardProps,
  PermissionsCardState
> {
  // eslint-disable-line react/prefer-stateless-function

  static defaultProps: Partial<PermissionsCardProps> = {
    showToggleIcon: false,
    allowHoverEffect: true,
  };

  state: PermissionsCardState = {
    isExpanded: false,
  };

  render() {
    const { isExpanded } = this.state;
    const {
      allowHoverEffect,
      table,
      header,
      showToggleIcon,
      cardFooterContent,
    } = this.props;
    const TheCard = allowHoverEffect ? HoverCard : Card;
    return (
      <TheCard>
        <CardContent
          className={showToggleIcon ? 'c-pointer' : ''}
          onClick={() => this.setState({ isExpanded: !isExpanded })}
        >
          <HeaderRow className="d-flex flex-row">
            <FixedTable showborder={false} size="sm">
              <tbody>{header}</tbody>
            </FixedTable>
            {showToggleIcon && (
              <ToggleContainer className="ml-auto">
                {isExpanded ? <ChevronUp /> : <ChevronDown />}
              </ToggleContainer>
            )}
          </HeaderRow>
        </CardContent>
        {showToggleIcon && (
          <Collapse isOpen={isExpanded}>
            <CardContent>
              <Form>{table}</Form>
            </CardContent>
            {cardFooterContent && (
              <CardActions>{cardFooterContent}</CardActions>
            )}
          </Collapse>
        )}
      </TheCard>
    );
  }
}

export default PermissionsCard;



