/**
 *
 * PermissionsCardTitleSection
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';

const IconAndTitleWrapper = styled.div`
  font-size: 2.5rem;
  align-items: center;
`;

const TitleWrapper = styled.h4`
  margin-left: 0.75rem;
`;

interface PermissionsCardTitleSectionProps {
  icon: React.ComponentType<{}>;
  title: FormattedMessage.MessageDescriptor;
}

const PermissionsCardTitleSection: SFC<PermissionsCardTitleSectionProps> = (
  props,
) => {
  return (
    <IconAndTitleWrapper className="d-flex flex-row">
      <props.icon />
      <TitleWrapper>
        <FormattedMessage {...props.title} />
      </TitleWrapper>
    </IconAndTitleWrapper>
  );
};

export default PermissionsCardTitleSection;



