/**
 *
 * FixedTable
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';
import { omit } from 'lodash';

import { Table, TableProps } from 'reactstrap';

const TableContainer = styled.div`
  td {
    ${(props: FixedTableProps) =>
      props.showborder
        ? 'border-top: 1px solid #e9ecef;'
        : 'border: none !important'};
  }
`;

const TableWrapper = styled(Table)`
  table-layout: fixed;
  margin-bottom: 0;
  td span,
  th span {
    margin-left: 0.25rem;
  }
  tr td:not(:first-child) {
    color: gray;
  }
`;

interface FixedTableProps extends TableProps {
  showborder?: boolean;
}

const defaultProps: Partial<FixedTableProps> = {
  showborder: true,
};

const FixedTable: SFC<FixedTableProps> = (props = { showborder: true }) => {
  const formatted = omit(props, 'showborder');
  return (
    <TableContainer showborder={props.showborder}>
      {' '}
      <TableWrapper {...formatted} />
    </TableContainer>
  );
};

FixedTable.defaultProps = defaultProps;

export default FixedTable;



