import { all, takeEvery, select, put, call } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import { ActionConstants as ActionConstantsConsole } from 'containers/Console/actions/rolesConstants';
import { selectRoles, selectRoleByName } from '../Console/selectors/roles';
import { Entities, RoleModel } from 'cb-utils/console-entity-models';
import { push } from 'react-router-redux';
import {
  InitRoleDetail,
  DeleteRoleRequest,
  createDeleteRoleError,
  createDeleteRoleSuccess,
} from './actions';
import {
  setModifiableRole,
  createSetModifiableItemTouched,
} from 'containers/Console/actions/modifiableEnitites';
import { selectEntities } from 'containers/Console/consoleSelectors';
import { EntityModels } from 'containers/Console/state';
import {
  rolesActions,
  serviceActions,
  portalActions,
  collectionActions,
} from '../Console/ducks';
import { getPathParams } from 'cb-utils/getPathParams';
import { deleteRole } from 'cb-utils/services/roles';
import { SaveRoleSuccess } from '../Console/actions/roles';
import { overallUrls } from 'cb-utils/getUrls';

let roleName: string;

function* fetchAllAssets(sysKey: string) {
  const entities: EntityModels = yield select(selectEntities());
  if (!entities.services.hasFetched) {
    yield put(serviceActions.fetchAll.request(sysKey));
  }
  if (!entities.portals.hasFetched) {
    yield put(portalActions.fetchAll.request(sysKey));
  }
  if (!entities.collections.hasFetched) {
    yield put(collectionActions.fetchAll.request(sysKey));
  }
}

function* goToRolesOverview() {
  yield put(push(overallUrls.roles()));
}

function* rolesLoaded() {
  const myRole: RoleModel = yield select(selectRoleByName(roleName));
  if (myRole) {
    // set to store
    yield put(setModifiableRole(myRole));
    yield fetchAllAssets(getPathParams().systemKey);
  } else {
    yield goToRolesOverview();
  }
}

function* callInit(action: InitRoleDetail) {
  roleName = action.payload.name;
  const roles: Entities<RoleModel> = yield select(selectRoles());
  if (!roles.isFetching) {
    yield put(rolesActions.fetchAll.request(action.payload.systemKey));
  }
}

export function* initSaga() {
  yield takeEvery(ActionConstants.INIT_ROLE_DETAIL, callInit);
}

export function* rolesLoadedSaga() {
  yield takeEvery(rolesActions.fetchAll.successType, rolesLoaded);
}

function* redirectOnSaveSaga() {
  // core update is taken care of in 'containers/Console/sagas/roles.ts'
  yield takeEvery(ActionConstantsConsole.SAVE_ROLE_SUCCESS, goToRolesOverview);
}

function* callDeleteRole(action: DeleteRoleRequest) {
  try {
    const sysKey = action.payload.systemKey;
    const role = action.payload.role;
    yield call(deleteRole, sysKey, role.ID);
    yield put(createDeleteRoleSuccess(sysKey, role));
    yield goToRolesOverview();
  } catch (e) {
    yield put(createDeleteRoleError(e));
  }
}

function* deleteRoleSaga() {
  yield takeEvery(ActionConstants.DELETE_ROLE_REQUEST, callDeleteRole);
}

function* markSaved() {
  yield takeEvery(ActionConstantsConsole.SAVE_ROLE_SUCCESS, function*(
    action: SaveRoleSuccess,
  ) {
    yield put(
      createSetModifiableItemTouched({
        pkColumn: 'name',
        assetClass: 'roles',
        item: action.payload,
        touched: false,
      }),
    );
  });
}

// All sagas to be loaded
export default function*() {
  yield all([
    initSaga(),
    rolesLoadedSaga(),
    deleteRoleSaga(),
    markSaved(),
    redirectOnSaveSaga(),
  ]);
}



