/**
 *
 * TypeaheadWithSubmit
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Button } from 'reactstrap';

import CbTypeahead, {
  IProps as CbTypeaheadProps,
} from 'components/CbTypeahead';
import { FormattedMessage } from 'react-intl';
import messages from 'components/CbTagInput/messages';
import myMessages from './messages';
import { KeyboardReturnIcon } from '../Icons';

const TypeaheadContainer = styled.div`
  min-width: 20rem;
  display: inline-block;
`;

interface TypeaheadWithSubmitProps extends CbTypeaheadProps {
  onSubmit: (val: string) => void;
  addMessage?: FormattedMessage.MessageDescriptor;
}

interface IState {
  newEntry: string;
  isMenuOpen: boolean;
}
type TypeaheadRef = any;

type MyEvent = Event &
  React.KeyboardEvent<HTMLInputElement> & {
    target: {
      value: string;
    };
  };

class TypeaheadWithSubmit extends React.Component<
  TypeaheadWithSubmitProps,
  IState
> {
  typeaheadRef: TypeaheadRef;

  state: IState = {
    newEntry: '',
    isMenuOpen: false,
  };

  static defaultProps: Partial<TypeaheadWithSubmitProps> = {
    addMessage: messages.add,
  };

  render() {
    const { addMessage, ...rest } = this.props;
    return (
      <React.Fragment>
        <TypeaheadContainer>
          <CbTypeahead
            {...rest}
            onInputChange={this.handleChange}
            onKeyDown={this.handleKeyDown}
            onChange={(selected: string) => {
              this.handleAdd(selected);
            }}
            onMenuShow={() => this.toggleMenuState(true)}
            onMenuHide={() => this.toggleMenuState(false)}
            getRef={(r) => {
              this.typeaheadRef = r;
            }}
          />
        </TypeaheadContainer>
        <Button
          onClick={() => this.handleAdd(this.state.newEntry)}
          className="ml-2"
          color="primary"
          data-cy="typeahead-submit-btn"
        >
          <FormattedMessage {...this.props.addMessage} />
          {/* TODO: only show the 'return' hint when a user has entered text or if they're going through the typeahead options; 
        currently the check for 'isMenuOpen' is primitive but react-bootstrap-typeahead doesn't give us a way to know if the user is actually arrowing through the options */}
          {(this.state.newEntry !== '' || this.state.isMenuOpen) && (
            <small>
              &nbsp; (
              <FormattedMessage {...myMessages.return} />
              &nbsp;
              <KeyboardReturnIcon />
              )
            </small>
          )}
        </Button>
      </React.Fragment>
    );
  }

  toggleMenuState = (isMenuOpen: boolean) => {
    this.setState({
      isMenuOpen,
    });
  };

  handleChange = (val: string) => {
    this.setState({ newEntry: val });
  };

  handleKeyDown = (e: MyEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      // todo: figure out how to avoid calling CHANGE_PERMISSION twice; currently handleAdd is being called here and by onChange
      this.handleAdd(e.target.value);
    }
  };

  handleAdd = (val: string) => {
    this.props.onSubmit(val);
    if (this.state.newEntry !== '') {
      this.setState({
        newEntry: '',
      });
    }

    this.typeaheadRef.getInstance().clear();
  };
}

export default TypeaheadWithSubmit;



